import { Cube, CubeSide } from "@/types/Cube";
import { AppSettings } from "@/types/Settings";
import { Simulation, SimulationStep } from '../types/Simulation';
import { getRandom } from './getRandom';


const createSimulation = (
    settings: AppSettings,
    cubes: Record<string, Cube>,
) => {
    const rounds: SimulationStep[] = [];
    let shipHp = settings.ship_hp;
    let enemyHp = settings.enemy_hp;
    const dice = cubes[settings.dice];

    while( shipHp > 0 && enemyHp > 0){
        const shipStartHp = shipHp,
            enemyStartHp = enemyHp; 
        
        const rolls = new Array(settings.dice_amount)
            .fill(null)
            .map(() => getRandom(0, dice.sides.length - 1));

        const rollsDelta = rolls.reduce<CubeSide>((acc, sideIndex) => {
            acc.attack += dice.sides[sideIndex].attack;
            acc.deffence += dice.sides[sideIndex].deffence;
            return acc;
        }, {
            attack: 0,
            deffence: 0
        });

        shipHp = shipHp - Math.max(0, settings.enemy_attack - rollsDelta.deffence);
        enemyHp = enemyHp - rollsDelta.attack;

        rounds.push({
            shipStartHp,
            enemyStartHp,
            diceRolls: rolls.map(i => dice.sides[i]),
            shipEndHp: shipHp,
            enemyEndHp: enemyHp,
        })
    }

    return rounds;
}

export const runSimulations = (
    settings: AppSettings,
    cubes: Record<string, Cube>,
): Simulation[] => {
    return new Array(settings.sim_number).fill(null).map((_, i) => ({
        id: i,
        steps: createSimulation(settings, cubes)
    }))
}