<template>
    <div>
        Simulations number: <input type="number" v-model="simNumber" />
        <button @click="$emit('simulate')">
            Run Simulations
        </button>
        <div>
            Ship HP: <input type="number" v-model="shipHp" />
            Enemy HP: <input type="number" v-model="enemyHp" />
            Enemy attack: <input type="number" v-model="enemyAttack" />
            <br/>
            Dice: <select v-model="dice">
                <option v-for="cube of dices" :key="cube.id" :value="cube.id">{{ cube.name }}</option>
            </select>
            Dice amount: <input type="number" v-model="diceAmount" />
        </div>
    </div>
</template>
<script lang="ts" setup>
import { PropType, computed } from 'vue';
import { AppSettings } from '../types/Settings';
import { Cube } from '../types/Cube';
import { valToNumber } from '../utils/valToNumber';
const settings = defineModel<AppSettings>({ required: true });
const props = defineProps({
    cubes: {
        type: Object as PropType<Record<string, Cube>>,
        required: true
    }
})
const emit = defineEmits<{
    (e: 'simulate')
}>();

const dices = computed(() => Object.values(props.cubes));

const shipHp = computed({
    get() {
        return settings.value.ship_hp
    },
    set(val) {
        settings.value = {
            ...settings.value,
            ship_hp: valToNumber(val),
        }
    }
})

const enemyHp = computed({
    get() {
        return settings.value.enemy_hp
    },
    set(val) {
        settings.value = {
            ...settings.value,
            enemy_hp: valToNumber(val),
        }
    }
})

const dice = computed({
    get() {
        return settings.value.dice;
    },
    set(val) {
        settings.value = {
            ...settings.value,
            dice: valToNumber(val),
        }
    }
})

const enemyAttack = computed({
    get() {
        return settings.value.enemy_attack;
    },
    set(val) {
        settings.value = {
            ...settings.value,
            enemy_attack: valToNumber(val),
        }
    }
})

const simNumber = computed({
    get() {
        return settings.value.sim_number;
    },
    set(val) {
        settings.value = {
            ...settings.value,
            sim_number: valToNumber(val),
        }
    }
})

const diceAmount = computed({
    get() {
        return settings.value.dice_amount
    },
    set(val) {
        settings.value = {
            ...settings.value,
            dice_amount: valToNumber(val),
        }
    }
})



</script>
<style scoped lang="less">
</style>