<template>
<div class="cubes">
    <button @click="addCube">
        Add new Cube
    </button>
    Sides number <input type="number" v-model="sidesNumber"/>
    <div class="cubes-cube" v-for="cube of cubeList" :key="cube.id">
        <button @click="() => deleteCube(cube.id)">
            Delete cube    
        </button>
        <AppCube
            :model-value="cube"
            @update:model-value="updateCube"
        />
    </div>
</div>
</template>
<script lang="ts" setup>
import { Cube } from '@/types/Cube';
import { createNewCube } from '@/utils/createCube';
import { computed, ref } from 'vue';
import AppCube from './AppCube.vue';

const cubes = defineModel<Record<string, Cube>>({ required: true });
const sidesNumber = ref(6);


const cubeList = computed(() => {
    return Object.values(cubes.value)
});

const nextCubeId = computed(() => {
    const max = Math.max(...Object.keys(cubes.value).map(Number));
    return isFinite(max) ? max + 1 : 0;
});

const addCube = () => {
    cubes.value = {
        ...cubes.value,
        [nextCubeId.value]: createNewCube(
            nextCubeId.value,
            sidesNumber.value
        )
    }
}

const updateCube = (
    cube: Cube
) => {
    cubes.value = {
        ...cubes.value,
        [cube.id]: cube
    }
}

const deleteCube = (id: number) => {
    const {[id]: deletedCube, ...rest} = cubes.value;
    cubes.value = rest;
}
</script>
<style lang="less" scoped>
.cubes {
    padding: 10px;
}
.cubes-cube {
    margin: 20px 0;
    padding: 10px;
    &:nth-child(2n) {
        background-color: rgba(0, 0, 255, 0.1);
    }
}
</style>