import { Cube } from "@/types/Cube";

export const createNewCube = (
    id: number,
    sidesNumber: number,
): Cube => {
    return {
        id,
        name: 'New cube',
        sides: new Array(sidesNumber).fill({
            attack: 0,
            deffence: 0
        }),
        color: ''
    }
}

export const createDefaultCube = (): Cube => {
    return {
        id: 0,
        name: 'Default cube',
        sides:[
            { attack: 0, deffence: 0 },
            { attack: 1, deffence: 0 },
            { attack: 2, deffence: 0 },
            { attack: 0, deffence: 1 },
            { attack: 0, deffence: 2 },
            { attack: 2, deffence: 2 },
        ],
        color: '',
    }
}