<template>
    <div>
        Cube name: <input v-model="name">
        <div>
            <label>Sides</label>
            <div v-for="(side, index) of cubeModel.sides" :key="index">
                Attack: <input type="number" :value="side.attack" @change="(e) => changeSide(e.target.value, 'attack', index)"/> 
                Deffence <input type="number" :value="side.deffence" @change="(e) => changeSide(e.target.value, 'deffence',index)"/>
            </div>
        </div>
    </div>
</template>
<script lang="ts" setup>
import { computed } from 'vue';
import { Cube, CubeSide } from '../types/Cube';
import { valToNumber } from '../utils/valToNumber';

const cubeModel = defineModel<Cube>({ required: true });

const name = computed({
    get() {
        return cubeModel.value.name;
    },
    set(val) {
        cubeModel.value = {
            ...cubeModel.value,
            name: val
        }
    }
});

const changeSide = (
    value: unknown,
    key: keyof CubeSide,
    index: number,
) => {
    const newSide: CubeSide = {
        ...cubeModel.value.sides[index],
        [key]: valToNumber(value)
    };
    cubeModel.value = {
        ...cubeModel.value,
        sides: cubeModel.value.sides.map((oldSide, sideIndex) => sideIndex === index ? newSide : oldSide)
    }
}

</script>
