<template>
  <div class="layout">
    <main class="layout-main">
      <div class="layout-header">
        <AppHeader
          v-model="settings"
          :cubes
          @simulate="onSimulate"
        />
      </div>
      <div class="layout-body">
        <AppSimulations
          v-if="simulations.length"
          :simulations
        />
      </div>
    </main>
    <aside class="layout-setting">
      <AppCubes
        v-model="cubes"
      />
    </aside>
  </div>
</template>

<script lang="ts" setup>
import { ref } from 'vue';
import { Cube } from '@/types/Cube';
import { AppSettings } from '@/types/Settings';
import { createDefaultCube } from '@/utils/createCube';
import { Simulation } from '@/types/Simulation';
import { createDefaultSetting } from '@/utils/createSettings';
import { validateSettings } from '@/utils/validateSettings';
import { runSimulations } from '@/utils/createSimulations'
import AppCubes from '@/components/AppCubes.vue';
import AppHeader from '@/components/AppHeader.vue';
import AppSimulations from '@/components/AppSimulations.vue';

const defaultCube = createDefaultCube();
const cubes = ref<Record<string, Cube>>({[defaultCube.id]: defaultCube});
const settings = ref<AppSettings>(createDefaultSetting());
const simulations = ref<Simulation[]>([]);

const onSimulate = () => {
  try {
    validateSettings(
      settings.value,
      cubes.value
    );
    simulations.value = runSimulations(
      settings.value,
      cubes.value
    );
  } catch(e) {
    alert(e);
  }
}

</script>

