import { Cube } from '@/types/Cube';
import { AppSettings } from '@/types/Settings';
export const validateSettings = (
    settings: AppSettings,
    cubes: Record<string, Cube>,
) => {
    const {
        dice,
        dice_amount,
        enemy_attack,
        ship_hp,
        sim_number,
        enemy_hp
    } = settings;

    if (!cubes[dice]) {
        throw 'Dice don\'t exist';
    }

    if (cubes[dice].sides.length < 2) {
        throw 'Dice has no sides';
    }

    if (dice_amount < 1) {
        throw 'Invalid dice amount';
    }

    if (enemy_attack < 0) {
        throw 'Enemy is enemy and cnnot heal the ship'
    }

    if (ship_hp < 1) {
        throw 'Dead ship travelling is not recomended'
    }

    if (sim_number < 1) {
        throw 'No simulations number provided'
    }

    if (enemy_hp < 1) {
        throw 'Enemy should have some hp';
    }
}
